/* Navbar.css */
.navbar {
    background-color: #333;
    color: #fff;
    padding: 20px;
    display: flex;
    justify-content: space-between; /* Align items horizontally with space between them */
    align-items: center; /* Align items vertically in the center */
  }
  
  .navbar h1 {
    margin: 0;
    padding: 0;
  }
  
  .navbar ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex; /* Display list items as flex items */
  }
  
  .navbar ul li {
    margin-left: 20px;
  }
  
  .navbar ul li:first-child {
    margin-right: 20px; /* Add margin to the right of the first list item */
  }
  
  .navbar ul li:last-child {
    margin-left: auto; /* Push the last item to the right */
  }
  
  .navbar ul li a {
    color: #fff;
    text-decoration: none;
  }
  